

.bookrepay{
  height: 100vh;
  background-color: #fff;
  display: flex;
  justify-content: center;
  flex-direction: column;
  overflow: hidden;


  .repaybox{
    flex: 1;
    overflow-y: scroll;



    .sys{
      width: 100%;
      height: 80px;
      display: flex;
      justify-content: center;
      align-items: center;
      // background-color: rgb(236, 9, 9);


      img{
        height: 80px;
        width: 80%;
        display: block;

      }

      .search{
        width: 20%;

        height: 80px;
        // background-color: rgb(243, 210, 210);
        text-align: center;
        line-height: 80px;


        span{
          font-size: 50px;
        }
      }
    }
    .txm p{
      height: 20px;
      text-align: center;
    }
  }
}

.searchBtn{
  height: 30px;

  
  p{
    width: 70%;
    height: 30px;
    float: left;
    line-height: 30px;
    padding-left: 15px;
    box-sizing: border-box;
    color: yellowgreen;
  }

  .icon-sousuo{
    display: block;
    width: 20%;
    height: 30px;
    font-size: 18px;
    line-height: 30px;
    float: right;
    background-color: #ccc;
    text-align: center;
    line-height: 30px;
  }
}

.searchlist{
  overflow: hidden;
  margin-top: 30px;
  display: flex;
  justify-content: center;
}


.errorTip{
  height: 50px;
  color: red;
  padding-left: 15px;
  line-height: 50px;
  border: 1px solid #ddd;
  margin: 20px 0;
  border-left: 0;
  border-right: 0;
  
}




.resultlist{
  width: 95%;
  padding: 5px 15px;
  box-sizing: border-box;
  overflow: hidden;
  background-color: rgb(247, 233, 233);
  color: rgb(243, 45, 95);
  border-radius: 10px;

  .r_box{
    overflow: hidden;

    .spansty{
      width: 50%;
      float: left;
    }

    .readerinfo{
      overflow: hidden;
    }

    .bookinfo{
        margin: 10px 0;
        overflow: hidden;
    }

    .borrowcount{
      overflow: hidden;
    }

    .repayBtn{
      display: flex;
      justify-content: center;
      align-items: center;

      .btn{
        width: 50%;
        height: 30px;
        background-color: rgb(85, 109, 226);
        line-height: 30px;
        color: #fff;
      }
    }

  }
}